html, body {
  height: 100%;
  font-family: Varela Round, sans-serif;
}

input, button {
  font-family: inherit;
}

body {
  grid-template: "nav nav nav nav"
                 "map . main ." 1fr
                 / 50% 1fr auto 1fr;
  display: grid;
  overflow: hidden;
}

@media only screen and (max-width: 700px) {
  body {
    grid-template: "nav"
                   "map" 45vh
                   "main" 1fr
                   / 1fr;
  }

  main {
    place-self: center;
  }
}

#map {
  z-index: 0;
  grid-area: map;
}

main {
  max-width: 400px;
  grid-area: main;
  margin: 16px;
}

.guess-rows > div {
  justify-content: space-between;
  display: flex;
}

.empty-guess, .guess {
  height: 20px;
  background: #ccc;
  border-radius: 4px;
  margin: 4px 0;
  padding: 12px;
}

core-navbar {
  grid-area: nav;
}

form {
  display: flex;
}

form input {
  border: 1px solid #aaa;
  border-radius: 4px;
  padding: 12px;
  font-size: 20px;
}

form input[type="text"] {
  margin-right: 2px;
}

.display-scores {
  float: right;
  width: 50px;
  height: 20px;
  text-align: center;
  text-transform: uppercase;
  color: #f5f5f5;
  background-color: #ea596e;
  border-radius: 3px;
  justify-content: center;
  margin-top: 20px;
  padding: 13px 17px;
  font-size: 16px;
  display: flex;
}

.display-header {
  padding-right: 3px;
}

.autofill-wrapper {
  display: inline-block;
  position: relative;
}

.autofill-popup {
  z-index: 5;
  border: none;
  border-left: 1px solid #ddd;
  border-right: 1px solid #ddd;
  position: absolute;
  bottom: 100%;
  left: 0;
  right: 0;
}

.autofill-popup div {
  cursor: pointer;
  background-color: #fff;
  border-bottom: 1px solid #ddd;
  padding: 10px;
}

.autofill-popup div:hover {
  background-color: #eee;
}

.autofill-popup div.active {
  color: #fff;
  background-color: var(--theme) !important;
}

.answer {
  text-align: center;
}

.answer span {
  font-size: 64px;
}

.answer a {
  margin: 32px 0;
  font-size: 32px;
  text-decoration: none;
  display: block;
}

.answer button {
  padding: 8px;
  font-size: 24px;
}

